<script>
    var i = 0;
    var str = '';

    function input() {
        var divTyping = document.getElementById('typing');
        if (i <= str.length) {
            divTyping.innerHTML = str.slice(0, i++) + '|';
            window.timer = setTimeout(function () {
                input()
            }, 100);
        }
        else {
            divTyping.innerHTML = str;
            i = 0
        }
    }

    function clear() {
        clearTimeout(window.timer)
    }

    export default {
        name: "index",
        metaInfo: {
            title: '福分通-首页', // set a title
            meta: [{                 // set meta
                name: '福分通',
                content: '专注企业员工福利'
            }]
        },
        data() {
            return {
                select: 0,
                screenOn: 0,
                banner: [
                    'static/homepage/banner2.jpg',
