                    'static/homepage/banner4.jpg'
                ],
                welfarebg: ['static/bg1.png', 'static/bg2.png', 'static/bg3.png'],
                welfaCont: [
                    {
                        tile: '弹性福利发放方案',
                        cont1: '偶尔需要一些节日福利发放，不想太麻烦？',
                        cont2: '申请企业专属账户，灵活选择福利在线发放。通过积分充值，自定义发放到员工账户，员工自由选购福利商品。'
                    },
                    {
                        tile: '定制专属福利商城',
                        cont1: '通用商城凸显不出企业自身的形象及企业文化？',
                        cont2: '别担心，定制企业的专属商城，支持自定义UI界面，展现企业文化与形象，增强员工归属感；同时支持自有供应商接入，管理更便捷。'
                    },
                    {
                        tile: '海量商品系统直连',
                        cont1: '有商城，但商品不够丰富？',
                        cont2: '福分通作为商品供应商接入，支持定向接口开发对接，系统对接。'
                    }
                ],
                setChild: [
                    'static/index-icon-app.png',
                    'static/index-icon-dingding.png',
                    'static/index-icon-fuwuhao.png',
                    'static/index-icon-oa.png',
                    'static/index-icon-wangzhan.png',
                    'static/index-icon-weixin.png'
                ],
                screen: [
                    'static/app.png',
                    'static/dding.png',
                    'static/fwh.png',
                    'static/oa.png',
                    'static/web.png',
                    'static/wechat.png'
                ],
                notes: [
                    '支持接入企业自由员工福利平台，拥有成熟的对接方案',
                    '转为中小型企业打造的快速沟通、高效协作的即时通讯工具',
                    '业务支持嵌入企业服务号，实现一键登录，企业集中管理',
                    '泛微、云之家等OA办公系统',
                    '企业门户官网，或内部平台网站',
                    '企业微信是专注于企业的沟通工具，与微信共用账号体系'
                ],
                bannerIndex: 0,
                str: '',
                tim: setInterval(() => {
                    if (this.bannerIndex === 0) {
                        this.bannerIndex = this.banner.length - 1;
                        return
                    }
                    this.bannerIndex -= 1
                }, 5000)
            }
        },
        created() {

        },
        methods: {
            stopBanner() {
                this.clearInterval(this.tim)
            },
            typing(k) {
                i = 0;
                this.screenOn = k;
                str = this.notes[k];
                clear();
                input()
            }
        }
    }
</script>

